import React, { Component } from 'react';
import { Col, Form, Input, Select } from 'antd';
import { SelectAjax } from '../../../../components/common/index';
import { getListTown } from '../../../../services/town';

const createSelect = name => (
  <Select>
    {[1, 2, 3].map(item => (
      <Select.Option value={item}>{`${name} ${item}`}</Select.Option>
    ))}
  </Select>
);

@Form.create({ name: 'FormInfoAddrBasic' })
class FormInfoAddrBasic extends Component {
  state = {};

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {};
    return (
      <Form layout="vertical">
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Qu qun">
            {getFieldDecorator('homeVillage', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Quc tch">
            {getFieldDecorator('nationId', {})(createSelect('Quc tch'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="TP xut thn">
            {getFieldDecorator('comeFromId', {})(createSelect('Thnh ph'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="C thng tr">
            {getFieldDecorator('permanentAddress', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="X/Phng">
            {getFieldDecorator('village', {})(createSelect('X'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Tnh/Tp">
            {getFieldDecorator('townId', {})(<SelectAjax service={getListTown} />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Qun/Huyn">
            {getFieldDecorator('districtId', {})(createSelect('Huyn'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="a ch bo tin">
            {getFieldDecorator('emergencyAddress', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Ni  hin nay">
            {getFieldDecorator('liveAddress', {})(<Input />)}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoAddrBasic;



